import {
    Avatar, Button,
    Dialog,
    DialogContent,
    Grid,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Typography
} from "@mui/material";
import {useGetTokenQuery} from "../redux/services/dexscreener";
import clsx from "clsx";

import {makeStyles} from "@mui/styles";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        }
    },
    negative: {
        color: 'red'
    },
    positive: {
        color: 'green'
    }
}));

export default function TokenInfoDialog(props) {

    const {contract_address, network, open, close} = props;

    const {data, status} = useGetTokenQuery(contract_address, {
        skip: !open
    });

    return <Dialog open={open} fullScreen>
        <DialogContent>
            <Button onClick={close} style={{position: 'absolute', top: 15, right: 15, zIndex: 99999}}>Close</Button>
            <Grid container spacing={2}>
                {
                    status === 'fulfilled'
                    && data.pairs
                    && data.pairs.length !== 0
                    && <PairDetails data={data.pairs[0]}
                                    network={network}
                    />
                }
                {
                    status === 'fulfilled'
                    && data.pairs
                    && !data.pairs.length
                    && <Grid item xs={12}><Typography>No pairs found</Typography></Grid>
                }
            </Grid>
        </DialogContent>
    </Dialog>
}

function PairDetails(props) {

    const classes = useStyles();

    const {data, network} = props;

    const getColorClass = (value) => {
        if (value === 0) return '';

        return value > 0 ? classes.positive : classes.negative
    }

    return <Grid item xs={12}>
        <ListItem>
            <ListItemAvatar>
                <Avatar src={`https://coineus.app/assets/tokens/${network}/${data.baseToken.address}/logo.png`}/>
                {data.chainId && <span className={clsx(classes.networkChip, data.chainId)}/>}
            </ListItemAvatar>
            <ListItemText
                primary={data.baseToken.name}
                secondary={data.baseToken.symbol}
            />
        </ListItem>
        <ListItem>
            <ListItemText
                primary={CoineusUSDFormat(data.fdv)}
                secondary="Fully Diluted Value"
            />
        </ListItem>
        <ListItem>
            <ListItemText
                primary={CoineusUSDFormat(data.priceUsd)}
                secondary="Price in USD"
            />
            <ListItemText
                primary={CoineusCryptoFormat(data.priceNative)}
                secondary={`Price in ${data.quoteToken.symbol}`}
            />
        </ListItem>
        <ListItem>
            <ListItemText
                primary={CoineusUSDFormat(data.liquidity.usd)}
                secondary="Liquidity"
            />
            <ListItemText
                primary={CoineusCryptoFormat(data.liquidity.base)}
                secondary={data.baseToken.symbol}
            />
            <ListItemText
                primary={CoineusCryptoFormat(data.liquidity.quote)}
                secondary={data.quoteToken.symbol}
            />
        </ListItem>

        <Typography style={{paddingLeft: 15, marginTop: 10}}>Price Change</Typography>
        <ListItem>
            <ListItemText
                primary={data.priceChange.m5 + "%"}
                secondary="5 mins"
                className={getColorClass(data.priceChange.m5)}
            />
            <ListItemText
                primary={data.priceChange.h1 + "%"}
                secondary="1 hr"
                className={getColorClass(data.priceChange.h1)}
            />
            <ListItemText
                primary={data.priceChange.h6 + "%"}
                secondary="6 hrs"
                className={getColorClass(data.priceChange.h6)}
            />
            <ListItemText
                primary={data.priceChange.h24 + "%"}
                secondary="24 hrs"
                className={getColorClass(data.priceChange.h24)}
            />
        </ListItem>

        <Typography style={{paddingLeft: 15, marginTop: 10}}>Volume</Typography>
        <ListItem>
            <ListItemText
                primary={CoineusUSDFormat(data.volume.m5)}
                secondary="5 mins"
            />
            <ListItemText
                primary={CoineusUSDFormat(data.volume.h1)}
                secondary="1 hr"
            />
            <ListItemText
                primary={CoineusUSDFormat(data.volume.h6)}
                secondary="6 hrs"
            />
            <ListItemText
                primary={CoineusUSDFormat(data.volume.h24)}
                secondary="24 hrs"
            />
        </ListItem>

        <Typography style={{paddingLeft: 15, marginTop: 10}}>Transactions (Buys/Sells)</Typography>
        <ListItem>
            <ListItemText
                primary={`${data.txns.m5.buys}/${data.txns.m5.sells}`}
                secondary="5 mins"
            />
            <ListItemText
                primary={`${data.txns.h1.buys}/${data.txns.h1.sells}`}
                secondary="1 hr"
            />
            <ListItemText
                primary={`${data.txns.h6.buys}/${data.txns.h6.sells}`}
                secondary="6 hrs"
            />
            <ListItemText
                primary={`${data.txns.h24.buys}/${data.txns.h24.sells}`}
                secondary="24 hrs"
            />
        </ListItem>
    </Grid>
}