import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    wallet: {
        address: undefined,
        chain: undefined,
        type: undefined,

        balances: {
            avax: {},
            fuse: {},
            bnb: {},
            eth: {}
        },

        coineusInfo: undefined,
        fookInfo: undefined,
        fudInfo: undefined
    },
    prices: {
        avax: undefined,
        fuse: undefined,
        bnb: undefined,
        fusd: undefined,
        usdc: undefined,

        cns: undefined,
        fook: undefined,
        fud: undefined,
        feg: undefined,

        fUSDcontract: '0x249be57637d8b013ad64785404b24aebae9b098b',
        fUSDFUSEpair: '0x933a10d094592eb3f2a26bcb366472eba8868a66',
        CNSFUSEpair: '0x0f2e60b799f2237633754f8eec7970c125e15d75',

        USDCAVAXpair: '0xf4003f4efbe8691b60249e6afbd307abe7758adb',
        FOOKAVAXpair: '0x3433b681141B4506B71AEAd3120b264bd9c12863',
        FUDAVAXpair: '0x3e7f1923ddb73a05ca84721b92104a0dffff4005',

        BNBBUSDpair: '0x16b9a82891338f9bA80E2D6970FddA79D1eb0daE',
        FEGBNBpair: '0x2Aa763656A92ce1e6E560c3EA76b4C5fca7C6C14'
    },
    rpc: {
        fuse: 'https://rpc.fuse.io/',
        avax: 'https://api.avax.network/ext/bc/C/rpc',
        bnb: 'https://bsc-dataseed1.binance.org/',
        eth: 'https://cloudflare-eth.com/'
    }
};

const coineusSlice = createSlice({
    name: 'coineus',
    initialState,
    reducers: {

        updateWalletChain(state, action) {
            state.wallet.chain = action.payload
        },

        updateWalletAddress(state, action) {
            state.wallet.address = action.payload
        },

        updateWalletProviderType(state, action) {
            state.wallet.type = action.payload
        },

        updateWalletCoineusInfo(state, action) {
            state.wallet.coineusInfo = action.payload
        },

        updateWalletFOOKInfo(state, action) {
            state.wallet.fookInfo = action.payload
        },

        updateWalletFUDInfo(state, action) {
            state.wallet.fudInfo = action.payload
        },

        updateBalances(state, action) {
            if(!state.wallet.balances[action.payload.chain][action.payload.contract_address]){
                state.wallet.balances[action.payload.chain][action.payload.contract_address] = 0;
            }
            state.wallet.balances[action.payload.chain][action.payload.contract_address] = action.payload.balance;
        },

        resetWallet(state) {
            state.wallet.chain = undefined;
            state.wallet.address = undefined;
            state.wallet.type = undefined;
            state.wallet.coineusInfo = undefined;
            state.wallet.fookInfo = undefined;
            state.wallet.fudInfo = undefined;
        },

        updatePrices(state, action) {
            state.prices[action.payload.name] = action.payload.price;
        }

    }
});

export const {
    updateWalletChain,
    updateWalletAddress,
    updateWalletProviderType,

    updateBalances,

    updateWalletCoineusInfo,
    updateWalletFOOKInfo,
    updateWalletFUDInfo,

    resetWallet,
    updatePrices
} = coineusSlice.actions;

export default coineusSlice.reducer;
